<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()




  import SubDocument from "../../../helpers/time_entry.js"

  import DraggableItem from "../Lists/Nestable/DraggableItem.svelte"
  import DroppableDivider from "../Lists/Nestable/DroppableDivider.svelte"

  import QuickEntry from "../QuickEntry/TimeEntry.svelte"

  import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte'
  import Tooltip from '../../../components/Overlay/Tooltip/Tooltip.svelte'
  import EffortFormat from '../../../components/Format/EffortFormat.svelte'

  import DueDatePopover from '../Popovers/DueDate.svelte'
  import AssignmentPopover from '../Popovers/Assignment.svelte'


  import Section from "./_Section.svelte"



  export assignments = []



  export collection = "time_entries"
  export item = undefined
  export listId = uuid()
  export sameListOnly = true


  subDocument = new SubDocument


  onMount ()->
    switch collection
      when "tasks"
        subDocument = new Task
      when "documents"
        subDocument = new Document
      when "assignments"
        subDocument = new Assignment
      # }



  updateName = (e)->
    console.log e.detail
    change =
      id: item.id
      name: e.detail
    dispatch "update", change

  updateAttribute = (e)->
    console.log "ListItme.svelte updateAttribute"
    console.log e.detail
    change = e.detail
    # change["id"] = item.id
    changedTask = item[collection].find { id: change.id }
    Object.merge(changedTask, change)
    item[collection] = item[collection] # refresh array
    console.log change
    dispatch "update", change


  update = ()->
    dispatch "update"


  add = (event)->
    newRecord = subDocument.stub(event.detail)
    newRecord.ordinal = (item[collection] || []).length
    item[collection] = item[collection].add newRecord
    dispatch "update"


  saveName = (e)->
    change =
      id: item.id
      name: e.detail
    dispatch "update", change

  save = (e)->
    change = e.detail
    change["id"] = item.id
    dispatch "update", change



  style = ":hover {font-weight: bold}"





  dropEvent = (e)->
    refresh = true
    if e.detail.divider?
      console.log "shift ordinals"
      # up or down?
      unless e.detail.dragIndex == e.detail.dropIndex
        if e.detail.dropIndex < e.detail.dragIndex
          item[collection].forEach (item)->
            if item.ordinal >= e.detail.dropIndex
              item.ordinal = item.ordinal + 1
          dragged = item[collection].find {id: e.detail.dragItem.id }
          dragged.ordinal = e.detail.dropIndex
        else
          item[collection].forEach (item)->
            if item.ordinal <= e.detail.dropIndex
              item.ordinal = item.ordinal - 1
          dragged = item[collection].find {id: e.detail.dragItem.id }
          dragged.ordinal = e.detail.dropIndex - 1
      else
        refresh = false
    else
      dragged = item[collection].find {id: e.detail.dragItem.id }
      dropped = item[collection].find {id: e.detail.dropItem.id }
      dragged.ordinal = e.detail.dropItem.ordinal
      dropped.ordinal = e.detail.dragItem.ordinal

    # cleanup ordinals in case there are stragglers
    item[collection].sortBy "ordinal"
    item[collection].forEach (item, index)->
      item.ordinal = index
    ##console.log "---before"
    #console.log item[collection]
    item[collection] = item[collection]
    #console.log "sorted and fixed"
    #console.log item[collection]
    if refresh
      dispatch "update"

  sort = (list)->
    if item[collection]?
      return item[collection].sortBy "ordinal"
    else
      []

  `$: list = sort(item[collection])`

  #-- copypasta

</script>
<style lang="sass">
  svg
    &[state]
      &:hover
        .fill
          fill: var(--gray)
      .fill
        fill: var(--gray)
    &[state="empty"]
      &:hover
        .fill
          fill: var(--blue)
      .fill
        fill: var(--lightest-gray)

  a
    cursor: pointer

</style>


<Section name={collection.titleize()} {open}>

  {#if list && list.length}
    <div>
      <DroppableDivider index={0} {listId} {sameListOnly} on:drop={dropEvent} />
      {#each list as timeItem, index}
        <DraggableItem item={timeItem} index={index} {listId} {sameListOnly} on:drop={dropEvent}>

          <div layout="row">


            <div flex="initial">
              <div layout="row">



                <!-- due date menu -->
                <div flex="initial">
                  <DueDatePopover item={timeItem} value={timeItem.due_date} on:save={save} on:open on:close/>
                </div>
                <div>{timeItem.booking_date}</div>
                <!-- document is not empty -->

                <div flex="initial"><span spacer/></div>
                <div flex ellipsis>
                  <InlineInput bind:value={timeItem.effort_minutes} on:update={saveName}  {style} let:label>
                    <EffortFormat value={label}/>
                  </InlineInput>
                </div>
                {#if (timeItem.memo || "").length > 0}
                  <div flex="initial">
                    <Tooltip>
                      <div slot="tip">Has content</div>
                      <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24" {state}><path d="M0 0h24v24H0V0z" fill="none"/><path d="M14 2H4v20h16V8l-6-6zm2 16H8v-2h8v2zm0-4H8v-2h8v2zm-3-5V3.5L18.5 9H13z" class="fill"/></svg>
                    </Tooltip>
                  </div>
                {/if}

                  <div flex="initial">
                    <AssignmentPopover item={timeItem} value={timeItem.assigned_to_id} on:save={save} on:open on:close bind:items={assignments}/>
                  </div>

              </div>
            </div>

          </div>

          <div slot="menu">
            <svg class="menu" xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M12,16A2,2 0 0,1 14,18A2,2 0 0,1 12,20A2,2 0 0,1 10,18A2,2 0 0,1 12,16M12,10A2,2 0 0,1 14,12A2,2 0 0,1 12,14A2,2 0 0,1 10,12A2,2 0 0,1 12,10M12,4A2,2 0 0,1 14,6A2,2 0 0,1 12,8A2,2 0 0,1 10,6A2,2 0 0,1 12,4Z"/></svg>
          </div>
        </DraggableItem>
        <DroppableDivider item={timeItem} index={index + 1} {listId} {sameListOnly} on:drop={dropEvent} />
      {/each}
    </div>
  {/if}

  <QuickEntry on:add={add} stubPlaceholder="Add Time Entry"/>

</Section>
