var SubDocument;

SubDocument = (function() {
  class SubDocument {
    model() {
      return {
        // if sourceversion < latest version, then some upgrades may be required
        // if null then return latest
        id: null,
        type: this.type,
        name: null,
        due_date: null,
        priority: null,
        is_favorite: null,
        completion_date: null,
        estimate: null,
        progress: {
          percent_complete: 0,
          color: "light-gray"
        },
        ordinal: null,
        indent: 0,
        hierarchy: null,
        content: "",
        content_indexable: "",
        assigned_to: null,
        comments: [],
        time_entries: [],
        checklist_items: [],
        clips: [],
        attachments: [],
        created_at: null,
        updated_at: null,
        metadata: {},
        visits: 0,
        settings: {
          sections: {
            details: {
              expanded: true
            },
            description: {
              expanded: true
            },
            checklist: {
              expanded: true
            },
            time_entries: {
              expanded: true
            },
            comments: {
              expanded: true
            },
            clips: {
              expanded: true
            }
          }
        }
      };
    }

    upgradeModel(item) {
      var latest;
      // check version
      latest = this.model();
      delete item.description;
      if (item.settings.sections == null) {
        item.settings["sections"] = latest.settings.sections;
      }
      if (item.progress == null) {
        item["progress"] = {
          percent_complete: item.percent_complete,
          color: "gray"
        };
        delete item.percent_complete;
      }
      return item;
    }

    stub(override) {
      var base;
      base = this.model();
      base.id = uuid();
      base.name = `New ${this.type} created at ${Date.create().format('%Y-%m-%d %H:%M:%S')}`;
      base.created_at = Date.create();
      base.updated_at = Date.create();
      if (override != null) {
        return Object.merge(base, override, true);
      } else {
        return base;
      }
    }

    transformCollection(items) {
      var that;
      that = this;
      console.log(items);
      return items.map(function(item) {
        return that.transformModel(item);
      });
    }

    transformModel(item) {
      var base;
      base = this.model();
      base.id = item._id;
      return Object.merge(base, this.upgradeModel(item), true);
    }

    constructor(options) {
      var that;
      that = this;
      this.after_construct();
    }

    after_construct() {}

  };

  SubDocument.prototype.name = "Resource Name";

  SubDocument.prototype.type = "resource";

  SubDocument.prototype.version = 1;

  return SubDocument;

}).call(this);

module.exports = SubDocument;
