<script lang="coffeescript">

  import PageContent from "../_Common/PageContent.svelte"
  import PageHeader from "../_Common/PageHeader.svelte"
  import Toast from "../../components/Toast/Toast.svelte"

  import {createEventDispatcher} from "svelte"
  dispatch = createEventDispatcher()
  dispatch "toast"
  showToast = false
  toast = (e)->
    console.log "Toast!"
    console.log e.detail
    showToast = true
    hide = ()->
      showToast = false
    hide.delay (6).sToMilliseconds()

</script>


<svelte:head>
  <title>Settings</title>
</svelte:head>
<PageContent>
  <a name="top"></a>
  <PageHeader>Settings</PageHeader>


</PageContent>
<Toast show={showToast}/>
