<script lang="coffeescript">

  import Router from 'svelte-spa-router'


  # Common
  import FourOFour from './components/FourOFour.svelte'
  import LeftSplit from './components/Layout/LeftSplit.svelte'


  # Local


  import SideNavigator from './pages/_Common/SideNavigator.svelte'

  import Home from './pages/Home/Home.svelte'
  import Projects from './pages/Projects/Index.svelte'
  import Project from './pages/Projects/Show.svelte'
  import Document from './pages/Documents/Show.svelte'
  import Task from './pages/Tasks/Show.svelte'
  import Assignment from './pages/Assignments/Show.svelte'
  import Settings from './pages/Settings/Settings.svelte'


  routes =

    '/': Home
    '/projects': Projects
    '/projects/all': Projects
    '/projects/:id': Project
    '/projects/:project_id/tasks/:id': Task
    '/projects/:project_id/assignments/:id': Assignment
    '/projects/:project_id/documents/:id': Document



    '/settings': Settings
    '*': FourOFour


  appName = "Braintapper"
  window.db = new PouchDB('braintapper', {auto_compaction: true})
  db.createIndex
    index:
      fields: ["type"]



</script>


<svelte:head>
  <title>{ appName }</title>
</svelte:head>

<LeftSplit headerSize="40px" contentScrollable={true}>
  <div slot="nav">
    <SideNavigator/>
  </div>
  <div slot="default">
    <Router {routes}/>
  </div>
</LeftSplit>



<style lang="sass">
  div[slot="default"]
    padding: 48px 96px
</style>
