<script lang="coffeescript">

  import { push } from 'svelte-spa-router'

  import Resource from '../../helpers/project.js'


  # Component Import

  import InlineInput from '../../components/Form/InlineInput/InlineInput.svelte'
  import DeleteConfirmation from '../../components/Overlay/Modal/DeleteConfirmation.svelte'


  # Common Import

  import PageHeader from '../_Common/PageHeader.svelte'
  import Description from '../_Common/Sections/Description.svelte'
  import Checklist from '../_Common/Sections/Checklist.svelte'
  import Time from '../_Common/Sections/Time.svelte'
  import Comments from '../_Common/Sections/Comments.svelte'
  import Clips from '../_Common/Sections/ClipList.svelte'
  import Progress from '../_Common/Popovers/Progress.svelte'

  # Props
  export params = undefined
  export project = undefined
  export collection = undefined

  resource = new Resource

  item = undefined

  list = []

  getProject = ()->
    scb = (doc) ->
      project = doc
      console.log
      item = project[collection].find({id: params.id})
      # unless item? 404
    resource.selectOne(params.project_id, scb, null, null)

  getProject()

  updateProject = ()->
    scb =(updated)->
      #console.log "updated"
    ecb = (err)->
      console.error err
      getProject()
    resource.update project, scb, ecb, null

  updateItem = (e)->
    #console.log e.detail
    Object.merge item, e.detail
    updateProject()
    item = item # force reaction

  deferredUpdate = updateProject.debounce(500)

  showDeleteConfirmation = false

  deleteConfirmation = ()->
    showDeleteConfirmation = true

  deleteConfirmationCancel = ()->
    showDeleteConfirmation = false

  destroy = ()->
    project[collection].remove {id: params.id}
    scb =(updated)->
      push "/projects/#{project.id}" # go back to project page
    ecb = (err)->
      console.log "udpate err"
      console.log err
      getProject()
    resource.update project, scb, ecb, null

  jump = (anchorName)->
    elem = u("[name=#{anchorName}]")
    scrollContainer = u("body > main > section").first()
    scrollContainer.scrollTop = elem.first().offsetTop - 40

  expandState = (expanded)->
    ["details","description","checklist","time_entries","comments","clips"].forEach (section)->
      item.settings.sections[section].expanded = expanded

  setTitle = (val)->
    if val?
      return "#{project.name} > #{val.name}"
    else
      return ""

  setSubtitle = (val)->
    switch val
      when "tasks"
        "Task"
      when "assignments"
        "Assignments"
      when "documents"
        "Documents"
    #}


  `$: title = setTitle(item)`
  `$: subtitle = setSubtitle(collection)`


</script>

<svelte:head>
  <title>{title}</title>
</svelte:head>

<div>
   {#if item}
      <a name="top"></a>
      <PageHeader >
        <h6 slot="subtitle">
          <a href="#/projects/{project.id}"><em>{project.name}</em></a>
          &gt;
          {collection.titleize().singularize()}
        </h6>
        <div slot="icon"><Progress bind:value={item.progress} {item} on:update={updateItem} size={36}/></div>
        <InlineInput bind:value={item.name} on:update={deferredUpdate} style="margin-left: -4px"/>
      </PageHeader>


      <a name="description"></a>
      <Description bind:item={item} bind:open={item.settings.sections.description.expanded} on:update={deferredUpdate}/>

      <a name="checklist"></a>
      <Checklist bind:item={item} bind:open={item.settings.sections.checklist.expanded} on:update={deferredUpdate}/>

      <a name="clips"></a>
      <Clips bind:item={item} bind:open={item.settings.sections.clips.expanded} on:update={deferredUpdate}/>

      <a name="time"></a>
      <Time bind:item={item} bind:open={item.settings.sections.time_entries.expanded} on:update={deferredUpdate}/>

      <a name="comments"></a>
      <Comments bind:item={item} bind:open={item.settings.sections.comments.expanded} on:update={deferredUpdate}/>
    {/if}
  </div>
  <a on:click={deleteConfirmation}><em>Delete</em></a>


{#if showDeleteConfirmation}
  <DeleteConfirmation on:destroy={destroy} on:cancel={deleteConfirmationCancel}/>
{/if}


<style lang="sass">

  h6
    text-transform: uppercase
    margin-bottom: -8px
    color: var(--gray)
    letter-spacing: 0.75px

</style>
