<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export item = undefined
  export value = 0

  newValue = undefined

  import Popover from '../../../components/Overlay/Popover/Popover.svelte'
  import Tooltip from '../../../components/Overlay/Tooltip/Tooltip.svelte'

  # menu

  show = false

  triggerClick = ()->
    dispatch "open"
    show = true


  triggerRef = undefined
  progressMenuClick = (e)->
    console.log "progressmenuclick"


  cancel = ()->
    show = false
    dispatch "close"
    newValue = value

  save = ()->
    show = false
    progressItem = { estimated_hours: newValue }


    dispatch "close"
    dispatch "save", progressItem


  add = ()->
    show = false
    progressItem = { estimated_hours: newValue }


    dispatch "close"
    dispatch "save", progressItem



  markComplete = ()->
    newValue = 100

  newTime = 0

  clear = ()->
    newValue = null

  `$: newValue = value || 0`

  ###
    states
      no estimate
      has estimate
      has time
      has no time
      is under
      is exactly
      is over
  ###


</script>
