  input
    width: 100px
    display: inline-block
  [type="progress"]
    svg
      path.fill
        fill: var(--gray)

</style>

<div>

<div on:click={triggerClick} bind:this={triggerRef}>
  <Tooltip>
    <div slot="tip">
      {#if item.estimated_hours == null}<p>No Estimate</p>{/if}
      {#if item.estimated_hours != null}<p>Estimate: {item.estimated_hours} hours</p>{/if}

