<script lang="coffeescript">


  # Lib

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()
  import Clip from "../../../helpers/clip.js"


  # Components

  import Indent from "../Indent.svelte" # this is a bloody workaround - attribute was not updating on element
  import DraggableItem from "../../_Common/Lists/Nestable/DraggableItem.svelte"
  import DroppableDivider from "../../_Common/Lists/Nestable/DroppableDivider.svelte"
  import QuickEntry from "../../_Common/QuickEntry/Clip.svelte"
  import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte'


  import Section from "./_Section.svelte"
  import Menu from '../Menus/Clip.svelte'

  # Props

  export item = undefined

  export collection = "clips"
  export listId = uuid()
  export sameListOnly = true

  subDocument = new Clip


  style = ":hover {font-weight: bold}"


  copy = (val)->
    dummy = document.createElement("input")
    document.body.appendChild(dummy)
    dummy.setAttribute("id", "clip-#{val.id}")
    document.getElementById("clip-#{val.id}").value=val.content
    dummy.select()
    document.execCommand("copy")
    document.body.removeChild(dummy)
    # toast it!
    console.log "copied #{val.name}"


  toggleVisibility = (val)->
    console.log val
    thisItem = item.clips.find {id: val.id}
    thisItem.is_visible = !thisItem.is_visible
    item[collection] = item[collection]
    dispatch "update"


  showDeleteConfirmation = false

  showDeleteModal = ()->
    showDeleteConfirmation = true

  deleteConfirmationCancel = ()->
    showDeleteConfirmation = false

  destroy = (e)->
    console.log "ClipList.destroy"
    console.log e.detail
    item[collection].remove {id: e.detail.id}
    item[collection] = item[collection]
    dispatch "update"
    showDeleteConfirmation = false


  updateName = (e)->
    console.log e.detail
    change =
      id: item.id
      name: e.detail
    dispatch "update", change

  updateAttribute = (e)->

    change = e.detail
    changedTask = item[collection].find { id: change.id }
    if changedTask?
      Object.merge(changedTask, change)
      item[collection] = item[collection]
      dispatch "update", change
    else
      console.log "unchanged"


  update = ()->
    dispatch "update"

  add = (event)->
    console.log "add"
    console.log event.detail
    newRecord = subDocument.stub(event.detail)
    newRecord.ordinal = (item[collection] || []).length
    item[collection] = item[collection].add newRecord
    dispatch "update"






  dropEvent = (e)->
    refresh = true
    if e.detail.divider?
      console.log "shift ordinals"
      # up or down?
      unless e.detail.dragIndex == e.detail.dropIndex
        if e.detail.dropIndex < e.detail.dragIndex
          item[collection].forEach (item)->
            if item.ordinal >= e.detail.dropIndex
              item.ordinal = item.ordinal + 1
          dragged = item[collection].find {id: e.detail.dragItem.id }
          dragged.ordinal = e.detail.dropIndex
        else
          item[collection].forEach (item)->
            if item.ordinal <= e.detail.dropIndex
              item.ordinal = item.ordinal - 1
          dragged = item[collection].find {id: e.detail.dragItem.id }
          dragged.ordinal = e.detail.dropIndex - 1
      else
        refresh = false
    else
      dragged = item[collection].find {id: e.detail.dragItem.id }
      dropped = item[collection].find {id: e.detail.dropItem.id }
      dragged.ordinal = e.detail.dropItem.ordinal
      dropped.ordinal = e.detail.dragItem.ordinal

    # cleanup ordinals in case there are stragglers
    item[collection].sortBy "ordinal"
    item[collection].forEach (item, index)->
      item.ordinal = index
    ##console.log "---before"
    #console.log item[collection]
    item[collection] = item[collection]
    #console.log "sorted and fixed"
    #console.log item[collection]
    if refresh
      dispatch "update"


  rowEl = undefined

  sort = (list)->
    if item[collection]?
      return item[collection].sortBy "ordinal"
    else
      []

  getWidth = (el)->
    if el?
      el.clientWidth
    else
      0
  `$: list = sort(item[collection])`




</script>
<style lang="sass">
  .fill
    fill: var(--gray)
    &:hover
      fill: var(--blue)
</style>

<Section name={collection.titleize()} {open}>

  {#if list && list.length}
    <div>
      <DroppableDivider index={0} {listId} {sameListOnly} on:drop={dropEvent} />
      {#each list as clipItem, index}
        <DraggableItem item={clipItem} index={index} {listId} {sameListOnly} on:drop={dropEvent}>

          <div layout="row" bind:this={rowEl} >
            <Indent level={clipItem.indent}/>
            <div flex="initial" style="width: 40px">
              <button type="button" icon on:click={copy(clipItem)}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M16 1H4c-1.1 0-2 .9-2 2v14h2V3h12V1zm-1 4H8c-1.1 0-1.99.9-1.99 2L6 21c0 1.1.89 2 1.99 2H19c1.1 0 2-.9 2-2V11l-6-6zM8 21V7h6v5h5v9H8z" class="fill"/></svg>
              </button>
            </div>
            <div flex main style="width: {getWidth(rowEl) - 40 - (clipItem.indent * 24)}px">
              <InlineInput bind:value={clipItem.name} on:update={updateName} {style} let:label>
                <label>{ label || "No name"}</label>
              </InlineInput>

              <div ellipsis>

                <InlineInput bind:value={clipItem.content} on:update={updateName} {style} let:label>
                  {#if clipItem.is_visible}
                    { label || "No content"}
                  {:else}
                    <span type="hidden">Value is Hidden</span>
                  {/if}
                </InlineInput>


              </div>

            </div>

          </div>

          <div slot="menu">
            <Menu item={clipItem} on:update={updateAttribute} on:destroy={destroy}/>
          </div>
        </DraggableItem>
        <DroppableDivider item={clipItem} index={index + 1} {listId} {sameListOnly} on:drop={dropEvent} />
      {/each}
    </div>
  {/if}
  <QuickEntry on:add={add}/>


</Section>
